* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  overflow-x: hidden;
  &::-webkit-scrollbar {
    display: none;
  }
}
@media (max-width: 500px) {
  body {
    overflow-x: none;
  }
}

